@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
}

/* CSS Reset  */
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
    font-weight: normal;
}

a {
    text-decoration: none;
    color: inherit;
    background-color: transparent;
}

ul,
ol {
    list-style: none;
}

button,
input,
select,
textarea {
    font: inherit;
}

button,
[type="button"],
[type="reset"],
[type="submit"],
[role="button"] {
    cursor: pointer;
    color: inherit;
}

img,
video {
    display: block;
    max-width: 100%;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
}

td:not([align]),
th:not([align]) {
    text-align: left;
}
.sections {
    padding: 1rem;
}

.projects .projects__heading h1 {
    font-size: 1.35rem;
    font-weight: 600;
    text-align: center;
}

@media (min-width: 576px) {
    .sections {
        padding: 1rem 2.5rem;
    }

    .projects .projects__heading h1 {
        font-size: 1.6rem;
    }
}

@media (min-width: 768px) {
    .sections {
        padding: 1rem 4rem;
    }
}

@media (min-width: 992px) {
    .sections {
        padding: 0 5rem;
    }

    .projects .projects__heading h1 {
        font-size: 2.3rem;
    }

    .projects__cards {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: center;
                justify-content: center;
    }
}
.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0.5rem 0.7rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.navbar h1 {
    font-size: 1.75rem;
    font-weight: bold;
    color: #6D62FD;
}

.navbar .hamburger {
    height: 40px;
    width: 40px;
    background-color: white;
    outline: none;
    border: none;
}

.navbar .hamburger svg {
    color: #6D62FD;
}

.navbar .hamburger svg .close {
    display: none;
}

.navbar .menu {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    display: none;
}

.navbar .menu li {
    font-size: 1.05rem;
    font-weight: 500;
    display: block;
}

.navbar .menu li+li {
    margin-top: 1rem;
}

.navbar .menu li a {
    text-align: center;
    display: block;
}

.navbar.expanded .menu {
    display: block;
}

.navbar.expanded .hamburger .close {
    display: block;
}

.navbar.expanded .hamburger .open {
    display: none;
}

@media (min-width: 992px) {

    .navbar {
        padding: 0.5rem 2rem;
    }

    .navbar .hamburger {
        display: none;
    }

    .navbar .menu {
        display: block;
        -webkit-flex-basis: auto;
                flex-basis: auto;
    }

    .navbar .menu li {
        display: inline-block;
        font-weight: 600;
        font-size: 1.1rem;
        letter-spacing: 1px;
    }

    .navbar .menu li+li {
        margin-left: 1.5rem;
    }
}
.intro {
    padding: 0 0 3rem 0;
}

.intro .intro__text {
    margin: 1rem 0;
}

.intro .intro__text h3 {
    font-size: 1.2rem;
    text-transform: uppercase;
    color: grey;
    font-weight: 600;
    margin-bottom: 0.25rem;
}

.intro .intro__text h1 {
    font-size: 1.75rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.intro .intro__text p {
    font-size: 1.05rem;
}

.intro .intro__image {
    margin-top: 3rem;
}

@media (min-width: 576px) {

    .intro .intro__text {
        margin: 1rem 0 4rem 0;
    }

    .intro .intro__text h1 {
        font-size: 1.85rem;
    }

    .intro .intro__image img {
        margin: auto;
        max-width: 500px;
    }
}

@media (min-width:768px) {
    .intro .intro__image img {
        margin: auto;
        max-width: 600px;
    }
}

@media (min-width: 992px) {
    .intro {
        display: -webkit-flex;
        display: flex;
        padding: 9.5rem 0;
    }

    .intro .intro__text {
        -webkit-flex: 1 1;
                flex: 1 1;
    }

    .intro .intro__text h3 {
        font-size: 1.5rem;
    }

    .intro .intro__text h1 {
        font-size: 2.7rem;
    }

    .intro .intro__text p {
        font-size: 1.15rem;
    }

    .intro .intro__image {
        -webkit-flex: 1 1;
                flex: 1 1;
    }

    .intro .intro__image img {
        margin: auto;
        width: auto;
    }
}

@media (min-width: 1300px) {

    .intro .intro__text h3 {
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }

    .intro .intro__text h1 {
        font-size: 3.2rem;
        margin-bottom: 1.45rem;
    }

    .intro .intro__text p {
        font-size: 1.2rem;
    }
}
.project {
    position: relative;
    margin: 2rem auto 0 auto;
    height: 350px;
    width: 350px;
}

.project img {
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover;
}

.project__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    color: #ffffff;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: opacity 0.25s;
    padding: 20px;
}

.project__overlay {
    opacity: 0;
}

.project__overlay:hover {
    opacity: 1;
}

.project__title {
    font-size: 1.5rem;
    font-weight: bold;
}

.project__description {
    font-size: 1rem;
}

@media (min-width: 576px) {
    .project {
        margin: 3rem auto;
    }
}

@media (min-width: 992px) {
    .project {
        margin: 2rem;
    }
}
.contact-container h1 {
    font-size: 1.35rem;
    font-weight: 600;
    padding-top: 2rem;
    text-align: center;
}

.contact .contact__options {
    display: -webkit-flex;
    display: flex;
    font-size: 30px;
    padding: 3rem 0;
    -webkit-justify-content: center;
            justify-content: center;
}

.contact .contact__options a+a {
    margin-left: 1rem;
}

.contact .contact__options .mail {
    color: red;
}

.contact .contact__options .facebook {
    color: blue;
}

.contact .contact__options .linkedin {
    color: blue;
}

.contact .contact__options .telegram {
    color: #0088cc;
}

.contact .contact__options a:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.3s all ease-in-out;
}

.contact .contact__image img {
    display: none;
}

@media (min-width: 576px) {
    .contact-container h1 {
        font-size: 1.6rem;
    }

    .contact .contact__options {
        font-size: 35px;
        padding: 4rem 0;
    }

    .contact .contact__options a+a {
        margin-left: 1.2rem;
    }
}

@media (min-width: 768px) {
    .contact-container h1 {
        font-size: 2.3rem;
    }

    .contact .contact__options {
        font-size: 40px;
        padding: 5rem 0;
    }

    .contact .contact__options a+a {
        margin-left: 1.5rem;
    }
}

@media (min-width: 992px) {

    .contact {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
        padding: 7rem 0;
    }

    .contact .contact__image img {
        display: block;
        width: 450px;
    }

    .contact .contact__options {
        font-size: 45px;
    }

    .contact .contact__options a+a {
        margin-left: 2.3rem;
    }
}
